import React,{useState} from 'react'
import {Container,Card,Row,Col,Button} from 'react-bootstrap'
import ControlsMap from '../../../Properties/PropertiesConfiguration'
import CollapsibleComponent from '../TableProperties/CollapsibleComponent'
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css"
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker"
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn"
import SvgDeleteIcon from "../../../../Icons/DeleteIcon"
import AddRecordIcon from '../../../../Icons/AddRecordIcon';

const DataAdditionSection=(props)=>{
    const[width,setWidth] = useState();
    function handleCombo(comboId){
       var select = document.getElementById(comboId);
       var propertyName = select.getAttribute("name");
       var optionTag = select.options[select.selectedIndex];
       handleProperty(propertyName, optionTag.value);
    }
    function handleProperty(propertyName, propertyValue){
        
        props.selectedControl.style[propertyName] = propertyValue;
        props.applyChanges(props.formData); 
    }
   
   function handleButton(arg){//console.log(arg);
        props.setShowDataAdditionSection(arg);
   }
    var InputItems = [];
    if(props.data && props.data.columnList){
        props.data.columnList.map((obj,index)=>{
                if( obj && obj.style && obj.style.ColumnType){
                    InputItems.push(prepareHtmlTag(obj));
                }
        })
    }
    if(props.data && props.data.listviewframelayout && props.data.listviewframelayout.children && props.data.listviewframelayout.children[0].children){  
        props.data.listviewframelayout.children[0].children.map((obj,index)=>{
                if(obj && obj.style && obj.elem_type){
                    InputItems.push(prepareHtmlTag(obj));
                }
    
        })  
    }
    function prepareHtmlTag(obj) {  //console.log(obj);
           if(obj && obj.style && obj.style.ColumnType)
           {
            switch(obj.style.ColumnType.toLowerCase()){
                case "combo" : return (
                            <div className={styles.dataAdditionControlDiv}>
                    <p className={styles.dataAdditionControlColumnName}>{obj.style.ColumnName}</p>
                        <select className={styles.dataAdditionControl} name={obj.style.ColumnName} id={obj.id+obj.style.ColumnName} onChange={()=>handleCombo(obj.id+obj.style.ColumnName)}>
                        {/* {obj.children.map((optionTagData,optionCount)=>{
                            
                                return <option value={ControlsMap[key].children[optionCount].Value}></option>
                            
                        })} */}
                        </select>
                    </div>
                        )
                

                case "toggle" : return(
                <div className={styles.dataAdditionControlDiv}>
                        <p className={styles.dataAdditionControlColumnName} >{obj.style.ColumnName}</p>
                        <ToggleBtn toggleState={false} handleStyleChange={(value)=>handleProperty(obj.style.ColumnName, value)}/> 
                    </div>
                )

                case "textarea" : return(
                <div className={styles.dataAdditionControlDiv}>
                        <p className={styles.dataAdditionControlColumnName}>{obj.style.ColumnName}</p>
                        <textarea aria-label={obj.style.ColumnName}  className={styles.dataAdditionControl}/>
                    </div>
                ) 
                case "textbox" : return(
                    <div className={styles.dataAdditionControlDiv}>
                            <p className={styles.dataAdditionControlColumnName}>{obj.style.ColumnName}</p>
                            <input aria-label={"Column Property"} type="textbox" className={styles.dataAdditionControl}/>
                            
                    </div>
                )
                case "text" : return(
                    <div className={styles.dataAdditionControlDiv}>
                            <p className={styles.dataAdditionControlColumnName}>{obj.style.ColumnName}</p>
                            <input aria-label={"Column Name"} type="text" className={styles.dataAdditionControl}/>
                            
                    </div>
                )  
                case "checkbox" : return(
                    <div className={styles.dataAdditionControlDiv}>
                            <p className={styles.dataAdditionControlColumnName}>{obj.style.ColumnName}</p>
                            <input aria-label={"Column Property"} type="checkbox" className={styles.dataAdditionControl2}/>
                        </div>
                )
                case "radio" : return(
                    <div className={styles.dataAdditionControlDiv}>
                            <p className={styles.dataAdditionControlColumnName}>{obj.style.ColumnName}</p>
                            <input aria-label={"Column Property"} type="radio" className={styles.dataAdditionControl2}/>
                    </div>
                )
                case "datepick" : return(
                    <div className={styles.dataAdditionControlDiv}>
                            <p className={styles.dataAdditionControlColumnName}>{obj.style.ColumnName}</p>
                            <input aria-label={"Column Property"} type="date" className={styles.dataAdditionControl}/>
                    </div>
                    )    
          }
    }

    }
 const [col, setCol] = useState(4);
 function handleCol(id){
    var selectTag = document.getElementById(id);
    setCol(selectTag.options[selectTag.selectedIndex].value)
 }



return (
        <>
            <div className={styles.dataAdditionSectionParentDiv} control_id={props.controlId} elem_type="listview" style={props.mobileDisplay === false || props.tabDisplay === false ? {display:"none"} : {display:"block"}} >
                <div id="overflowDiv" className={styles.dataAdditionSectionOverflowDiv} control_id={props.controlId} elem_type="listview">
                    <div className={styles.dataAdditionSectionHeaderDiv} control_id={props.controlId} elem_type="listview">
                        <div className={[styles.dataAdditionSectionHeaderInnerDiv1,{col}].join(" ")} control_id={props.controlId} elem_type="listview">Data Addition</div>
                        {props && props.previewmode ?
                        null : 
                        <div className={styles.dataAdditionSectionHeaderInnerDiv2} control_id={props.controlId} elem_type="listview">COL
                            <select id="col-Selection" className={styles.dataAdditionSectionHeaderInnerDiv2Select} onChange={()=>handleCol("col-Selection")}>
                                <option></option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                                <option value="6">6</option>
                                <option value="12">12</option>
                            </select>
                        </div>}
                        
                        <div className={styles.dataAdditionSectionHeaderInnerDiv3}>
                            <Button disableFocusRipple
                disableRipple
                tabIndex={0} className={`${styles.dataAdditionSectionHeaderInnerDiv3Button}  formBuilder-MuiButton-outlined`}> <span><AddRecordIcon/></span>Add to table</Button>
                        </div>
                    </div>
                    <div className={styles.containerParentDiv} control_id={props.controlId} elem_type="listview">
                        <Container className={styles.rowContainer} control_id={props.controlId} elem_type="listview">
                            <Row style={{margin:"0"}} control_id={props.controlId} elem_type="listview">
                                {
                                    InputItems.map((item,index)=>{
                                        return <Col xs={col} className={styles.colDivListview}>{item}</Col>;
                                    })   
                                }
                                </Row>
                            </Container>
                    </div> 
               </div>
            </div>
        
        
       
        
        
        </>
    )

}

export default DataAdditionSection;