import React, { useState, useEffect, useRef } from "react";
import { Form } from "react-bootstrap";
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import styles2 from "../css/COLUMN.module.css";
import { StyleSheet } from "aphrodite";
import { CSSPreparation1 } from "../../components/Properties/CSSPreparation1.jsx";
import DataAdditionSection from "../../components/IFormDesignerLayout/IFormDesignerComponents/ListviewProperties/DataAdditionSection";
import PrepareCSSClass from "../../components/IFormDesignerLayout/PrepareCSSClass";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { useDispatch } from "../../Redux";
import { handleDisplayProperty } from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import CustomizedAlert from "../../ReusableComponents/Alert/CustomizedAlert";
import { GetState, GetStateFromData, useElementWidth } from "../../components/CustomHooks/CustomHooks";
import ActionHeader from "./TableComponent/ActionHeader";
import DataTable from "./DataTable";
import AdvanceListviewPopUp from "../../components/IFormDesignerLayout/IFormDesignerComponents/AdvanceListviewPopUp.jsx";
import { updateContextsStates } from "../../Redux/Actions/FormStateActions.js";

const ADVANCELISTVIEW = (props) => {
  const contexts = GetStateFromData(props.containerId);
  const gridContexts = GetStateFromData(props.containerId,"data2");
  const { tempCanWidth, controlSelected, renderAdvanceListView}  = GetState("tempCanWidth", "controlSelected","renderAdvanceListView");
  const [render, setRender] = useState(false);
  const {
    currentAdvanceListviewStateJson,
    openCanvasForPreview,
    openCanvasForPreviewAdvance,
  } = gridContexts.gridStates;

  const {
    IdPrecedence,
    device,
    deleteControl,
    showDataAdditionSection,
    setShowDataAdditionSection,
    formState2,
    setSelectedControl,
    setRecordCount,
    recordCount,
    openQuickToolFor,
  } = contexts.contexts;

  const conditionalQuickToolOpening = props.id === openQuickToolFor ? true : false;

  const dispatch=useDispatch()
  const actualControlId = props.data.id;
  let mappingId = props?.data?.["style"]?.["MappingId"];
  let userDefinedId = props?.data?.["style"]?.["CustomId"];
  let userDefinedControlId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);

  const data = props.data;

  const themeData = formState2.theme;
  const controlThemeData = themeData?.["TableStyle"];

  const isSubformInPage = props?.isSubformInPage || false;

  const [sortableColumns, setSortableColumns] = useState("");
  const [isSearched, setIsSearched] = useState(false);
  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
  const [selectedRowsList, setSelectedRowsList] = useState([]);
  const [defaultLabel, setDefaultLabel] = useState(data?.style?.["Label"] ? data.style["Label"] : data.id);
  const [rowData, setRowData] = useState([]);
  const [filterData, setFilterData] = useState([]);
  const [checkedKeys, setCheckedKeys] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [latestAddedRow, setLatestAddedRow] = useState(-1);
  const [tableData, setTableData] = useState(props.data || {});
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === "") {
      setDefaultLabel(props.data.style["Label"]);
    }
  }, [props.data]);

  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2?.style) {
      startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
      endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
      startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
      endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"]
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth
  ]);

  const tableTitleDefautStyle = {
    "font-family": "Open Sans",
    "font-weight": "Bold",
    "font-style": "normal",
    color: "#000000"
  };

  if (themeData?.["BasicTheme"]?.["FontColor"]) {
    tableTitleDefautStyle["font-family"] = themeData["BasicTheme"]["FontFamily"];
  }
  if (themeData?.["BasicTheme"]?.["FontSize"]) {
    tableTitleDefautStyle["font-size"] = themeData["BasicTheme"]["FontSize"];
  }

  if (controlThemeData?.["TableTitleFontFamily"]) {
    tableTitleDefautStyle["font-family"] = controlThemeData["TableTitleFontFamily"];
  }
  if (controlThemeData?.["TableTitleFontSize"]) {
    tableTitleDefautStyle["font-size"] = controlThemeData["TableTitleFontSize"];
  }
  if (controlThemeData?.["TableTitleFontWeight"]) {
    tableTitleDefautStyle["font-weight"] = controlThemeData["TableTitleFontWeight"];
  }
  if (controlThemeData?.["TableTitleFontColor"]) {
    tableTitleDefautStyle["color"] = `${controlThemeData["TableTitleFontColor"]} !important`;
  }

  const themeStyle = {
    tableTitleDefaultStyle: tableTitleDefautStyle
  };

  const jsonForTableTitle = {};
  if (props?.data?.style) {
    if (props.data.style["ListviewTitleFontFamily"]) {
      jsonForTableTitle["ListviewTitleFontFamily"] = props.data.style["ListviewTitleFontFamily"];
    }
    if (props.data.style["ListviewTitleFontSize"]) {
      jsonForTableTitle["ListviewTitleFontSize"] = props.data.style["ListviewTitleFontSize"];
    }
    if (props.data.style["ListviewTitleFontWeight"]) {
      jsonForTableTitle["ListviewTitleFontWeight"] = props.data.style["ListviewTitleFontWeight"];
    }
    if (props.data.style["ListviewTitleFontStyle"]) {
      jsonForTableTitle["ListviewTitleFontStyle"] = props.data.style["ListviewTitleFontStyle"];
    }
    if (props.data.style["ListviewTitleFontColor"]) {
      jsonForTableTitle["ListviewTitleFontColor"] = `${props.data.style["ListviewTitleFontColor"]} !important`;
    }
  }

  const preparedStyle2 = CSSPreparation1(jsonForTableTitle);
  const styleSheet2 = StyleSheet.create({
    styling: preparedStyle2
  });

  function generateColumn(id, columnName) {
    return { field: id, headerName: columnName, height: 36 };
  }

  const columns = [];
  if (props?.data?.columnList) {
    columns.push(generateColumn("All Column", "All Column"));
    props.data.columnList.map((obj, index) => {
      if (obj && obj.style["VisibleOnTable"] === "true" && obj.style["EnableSearch"] === "true") {
        columns.push(generateColumn(obj.style.ColumnName, obj.style.ColumnName));
      }
    });
  }

  const inputStylePadding = {
    paddingLeft: props?.data?.style?.["SpacingLeft"] || "0px",
    paddingRight: props?.data?.style?.["SpacingRight"] || "0px",
    paddingTop: props?.data?.style?.["SpacingTop"] || "0px",
    paddingBottom: props?.data?.style?.["SpacingBottom"] || "0px",
    position: "relative",
    display: "block"
  };

  if (!formState2[`advanceListviewRowDataCopy${props.data.id}`] && props.previewmode && props?.data?.rowData) {
    formState2[`advanceListviewRowDataCopy${props.data.id}`] = JSON.parse(JSON.stringify(props?.data?.rowData));
  }

  useEffect(() => {
    setTableData(props.data);
  }, [props.data?.style?.updateFormState]);

  const [openPopup, setOpenPopup] = useState(false);

  function gridPopup(gridType, jsonForListview={}, listViewAddRecordFrom, selectedRowIndexAdvance) {
    if (gridType === "advanceListviewPopupInPreview") {
      dispatch(updateContextsStates({
        key: props.containerId,
        value: {
          listViewAddRecordFrom,
          selectedRowIndexAdvance,
          advanceListviewStateJson: jsonForListview,
          currentAdvanceListviewStateJson: jsonForListview,
          openCanvasForPreview: "advanceListviewPopupInPreview",
          openCanvasForPreviewAdvance: "advanceListviewPopupInPreview",
        }
      }));
      setRender(!render);
      setOpenPopup(true);
      if(window.loading) {
        window.loading(false);
      }
    } else {
      dispatch(updateContextsStates({
        key: props.containerId,
        value: { openCanvasForPreview: gridType, subFormListviewJson: jsonForListview }
      }));
      setRender(!render);
    }
  }

  if (props.previewmode && data) {
    return (
      <>
        {data?.style?.["Visible"] === "true" && showDataAdditionSection === "Section above Listview" ? (
          <DataAdditionSection data={data} setShowDataAdditionSection={(arg) => setShowDataAdditionSection(arg)} />
        ) : null}

        {data?.style?.["Visible"] === "true" ? (
          <>
            <Form.Group
              style={mobileDisplay === false || tabDisplay === false ? { display: "none" } : inputStylePadding}
              className={styles2.tableStyle1 + " spacingThemeControls "}
              control_id={userDefinedControlId + "_group"}
              key={data.id}
              elem_type="advancelistview"
              frameindex={data.frameIndex}
              elementindex={data.elementIndex}
            >
              <PrepareCSSClass stylingData={themeStyle} />

              <ActionHeader
                type="advancelistview"
                data={data}
                id={props.id}
                isSubformInPage={isSubformInPage}
                userDefinedControlId={userDefinedControlId}
                styleSheet2={styleSheet2}
                selectedRowsList={selectedRowsList}
                handleSelected={props.handleSelected}
                containerId={props.containerId}
                tableData={tableData}
                previewmode={props.previewmode}
                recordCount={recordCount}
                setRecordCount={setRecordCount}
                setTableData={setTableData}
                setLatestAddedRow={setLatestAddedRow}
                setSelectedRowsList={setSelectedRowsList}
                setLoading={setLoading}
                checkedKeys={checkedKeys}
                setCheckedKeys={setCheckedKeys}
                rowData={rowData}
                setRowData={setRowData}
                gridPopup={gridPopup}
                setFilterData={setFilterData}
                filterData={filterData}
                setSearchValue={setSearchValue}
                searchValue={searchValue}
                sortableColumns={sortableColumns}
                setSortableColumns={setSortableColumns}
                isSearched={isSearched}
                setIsSearched={setIsSearched}
              />

              <div className={styles2.basicTableParentDivP}>
                <DataTable
                  id={data.id}
                  tableData={tableData}
                  previewMode={props.previewmode}
                  loading={loading}
                  containerId={props.containerId}
                  setCheckedKeys={setCheckedKeys}
                  gridPopup={gridPopup}
                  checkedKeys={checkedKeys}
                  data={rowData}
                  setData={setRowData}
                  setFilterData={setFilterData}
                  filterData={filterData}
                  setTableData={setTableData}
                  recordCount={recordCount}
                  setRecordCount={setRecordCount}
                  sortableColumns={sortableColumns}
                  setSortableColumns={setSortableColumns}
                  isSearched={isSearched}
                  setIsSearched={setIsSearched}
                />
              </div>
            </Form.Group>
{
  openCanvasForPreviewAdvance === "advanceListviewPopupInPreview" && 
            <AdvanceListviewPopUp
              {...props}
              open={openCanvasForPreviewAdvance === "advanceListviewPopupInPreview" && openPopup}
              setOpen={setOpenPopup}
              subFormListviewJson={currentAdvanceListviewStateJson}
              openCanvasForPreview={openCanvasForPreview}
              gridPopup={gridPopup}
            />
}
          </>
        ) : null}
      </>
    );
  } else if (data) {
    return (
        <div
          onClick={isSubformInPage ? {} : (e) => props.handleSelected(e, data.id, "n")}
          style={inputStylePadding}
          tabIndex="1"
          onKeyDown={(e) => props.handleSelected(e)}
        >
          <Form.Group
            className={styles2.tableStyle1 + " spacingThemeControls "}
            control_id={data.id}
            id={data.id}
            key={data.id}
            elem_type="advancelistview"
            frameindex={data.frameIndex}
            elementindex={data.elementIndex}
            onKeyDown={(e) => props.handleSelected(e)}
          >
            <PrepareCSSClass stylingData={themeStyle} />

            <ActionHeader
              type="advancelistview"
              data={data}
              id={props.id}
              isSubformInPage={isSubformInPage}
              userDefinedControlId={userDefinedControlId}
              styleSheet2={styleSheet2}
              selectedRowsList={selectedRowsList}
              handleSelected={props.handleSelected}
              containerId={props.containerId}
              tableData={data}
              previewmode={props.previewmode}
              recordCount={recordCount}
              setRecordCount={setRecordCount}
              setTableData={setTableData}
              setLatestAddedRow={setLatestAddedRow}
              setSelectedRowsList={setSelectedRowsList}
              setLoading={setLoading}
              checkedKeys={checkedKeys}
              setCheckedKeys={setCheckedKeys}
              rowData={rowData}
              setRowData={setRowData}
              setFilterData={setFilterData}
              filterData={filterData}
              setSearchValue={setSearchValue}
              searchValue={searchValue}
              sortableColumns={sortableColumns}
              setSortableColumns={setSortableColumns}
              isSearched={isSearched}
              setIsSearched={setIsSearched}
            />
            <div
              className={styles2.basicTableParentDivD}
              onClick={() => setSelectedControl(props.data)}
              control_id={props.data.id}
            >
              <DataTable
                id={data.id}
                tableData={props.data}
                previewMode={props.previewmode}
                loading={loading}
                containerId={props.containerId}
                setCheckedKeys={setCheckedKeys}
                checkedKeys={checkedKeys}
                data={rowData}
                setData={setRowData}
                setFilterData={setFilterData}
                filterData={filterData}
                setTableData={setTableData}
                recordCount={recordCount}
                setRecordCount={setRecordCount}
                sortableColumns={sortableColumns}
                setSortableColumns={setSortableColumns}
                isSearched={isSearched}
                setIsSearched={setIsSearched}
              />
            </div>
          </Form.Group>
          {controlSelected?.id === props?.id && (
            <ElemDeleteControl
              isSubformInPage={isSubformInPage}
              containerId={props.containerId}
              id={props.data.id}
              deleteControl={deleteControl}
              elemType={props.data?.elem_type}
              setDragging={props.setDragging}
              handleReorderStart={props.handleReorderStart}
              handleReorderDrop={props.handleReorderDrop}
              data={props.data}
              dragId={props.dragId}
              dragType={"column"}
            />
          )}
          {window?.openQuickTools && open && (
            <QuickTools containerId={props.containerId} selectedControl={props.data} open={open} setOpen={setOpen} />
          )}
        </div>
    );
  } else {
    return <></>;
  }
};

export default ADVANCELISTVIEW;
