import React,{useState} from 'react'
import {Card} from 'react-bootstrap'
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faAngleDown, faAngleUp } from "@fortawesome/free-solid-svg-icons";
import ControlsMap from '../../../Properties/PropertiesConfiguration'
import { useTranslation } from 'react-i18next'


function CollapsibleComponent(props){
   
    const [showCollapsibleBody, setShowCollapsibleBody] = useState(false);
    const { t } = useTranslation(['styles', 'translation']);
    return (
        <Card className={styles.configuration}>
                        <Card.Header className={styles.configurationHeader} onClick={()=>setShowCollapsibleBody(!showCollapsibleBody)}>
                        <div className={styles.configurationHeaderDiv} >
                            <span>{t(`translation:${props.heading}`)}</span>
                            {showCollapsibleBody === true ? <FontAwesomeIcon className={styles.downArrow} icon={faAngleUp}/>  : 
                            <FontAwesomeIcon className={styles.downArrow} icon={faAngleDown}/> }
                        </div>
                        </Card.Header>
        {showCollapsibleBody === true ? 
        <Card.Body className={  props.body.length === 0 ? styles.emptyCardBody : styles.cardBodyButtonProperties}>
            {props.body.map((item)=>{
                return item;
            })}   
        </Card.Body>
        : null }
                        
        </Card> )
}

export default CollapsibleComponent